"use client"

import { useRef, useMemo } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { Line } from '@react-three/drei'
import * as THREE from 'three'

interface NeuralNetworkBackgroundProps {
    speed: number
    intensity: number
    colorTheme: 'ocean' | 'forest' | 'sunset' | 'cyber' | 'plasma'
}

const themeColors = {
    ocean: { primary: '#3b82f6', secondary: '#06b6d4' },
    forest: { primary: '#10b981', secondary: '#34d399' },
    sunset: { primary: '#f59e0b', secondary: '#ec4899' },
    cyber: { primary: '#8b5cf6', secondary: '#ec4899' },
    plasma: { primary: '#ec4899', secondary: '#8b5cf6' }
}

function Network({ speed, intensity, colorTheme }: NeuralNetworkBackgroundProps) {
    const groupRef = useRef<THREE.Group>(null)

    const nodes = useMemo(() => {
        return Array.from({ length: 50 }, () => ({
            position: [
                (Math.random() - 0.5) * 20,
                (Math.random() - 0.5) * 20,
                (Math.random() - 0.5) * 10
            ],
            color: Math.random() > 0.5 ? themeColors[colorTheme].primary : themeColors[colorTheme].secondary
        }))
    }, [colorTheme])

    const connections = useMemo(() => {
        const conns = []
        for (let i = 0; i < nodes.length; i++) {
            for (let j = i + 1; j < nodes.length; j++) {
                const dist = Math.sqrt(
                    Math.pow(nodes[i].position[0] - nodes[j].position[0], 2) +
                    Math.pow(nodes[i].position[1] - nodes[j].position[1], 2) +
                    Math.pow(nodes[i].position[2] - nodes[j].position[2], 2)
                )
                if (dist < 5) {
                    conns.push([...nodes[i].position, ...nodes[j].position])
                }
            }
        }
        return conns
    }, [nodes])

    useFrame((state) => {
        if (!groupRef.current) return
        groupRef.current.rotation.y = state.clock.getElapsedTime() * speed * 0.05
        groupRef.current.rotation.x = Math.sin(state.clock.getElapsedTime() * speed * 0.02) * 0.1
    })

    const color = themeColors[colorTheme].primary

    return (
        <group ref={groupRef}>
            {/* Connections with pulsing effect */}
            {connections.map((c, i) => (
                <Line
                    key={i}
                    points={[[c[0], c[1], c[2]], [c[3], c[4], c[5]]]}
                    color={color}
                    lineWidth={0.5}
                    transparent
                    opacity={(0.05 + Math.sin(Date.now() * 0.001 + i) * 0.02) * intensity}
                    blending={THREE.AdditiveBlending}
                />
            ))}

            {/* Nodes with pulsing */}
            {nodes.map((node, i) => (
                <mesh key={i} position={node.position as any}>
                    <sphereGeometry args={[0.1, 16, 16]} />
                    <meshStandardMaterial
                        color={node.color}
                        emissive={node.color}
                        emissiveIntensity={intensity * (0.5 + Math.sin(Date.now() * 0.001 + i) * 0.2)}
                    />
                </mesh>
            ))}
        </group>
    )
}

export default function NeuralNetworkBackground({ speed, intensity, colorTheme }: NeuralNetworkBackgroundProps) {
    return (
        <Canvas camera={{ position: [0, 0, 20], fov: 60 }}>
            <ambientLight intensity={0.2} />
            <Network speed={speed} intensity={intensity} colorTheme={colorTheme} />
        </Canvas>
    )
}
